.happy-animate {
  position: relative; }

.animate__fadedown {
  opacity: 0; }

.animate__fadedown--inview {
  opacity: 1;
  transform: translate3d(0, 0px, 0) !important; }

.animate__fadein {
  opacity: 0; }

.animate__fadein--inview {
  opacity: 1; }

.animate__fadeleft {
  opacity: 0; }

.animate__fadeleft--inview {
  opacity: 1;
  transform: translate3d(0, 0px, 0) !important; }

.animate__faderight {
  opacity: 0; }

.animate__faderight--inview {
  opacity: 1;
  transform: translate3d(0, 0px, 0) !important; }

.animate__fadeup {
  opacity: 0; }

.animate__fadeup--inview {
  opacity: 1;
  transform: translate3d(0, 0px, 0) !important; }

.animate__zoomin {
  opacity: 0;
  transform: scale3d(0.3, 0.3, 0.3); }

.animate__zoomin--inview {
  opacity: 1;
  transform: scale3d(1, 1, 1); }
